import React,{ Component} from 'react';

import {Bar,/*Pie*/} from 'react-chartjs-2';
import {connect} from 'react-redux'
import {getDataLocalidades} from '../Opendata/data.js';


class Graphic extends Component{

    constructor(props){
        super(props);
        this.state={
            dataIsLoaded: false,
            Months:['January', 'February', 'March', 'April',
            'May', 'June', 'July', 'August', 'September',
            'October', 'November', 'December'],
            charData:{
                labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
                datasets: [{
                    label: 'Dataset 1',
                    borderWidth: 1,
                    data: [ 0,0,0,0,0,0,0 ],
                    borderColor: 'rbga(120,120,120,1)'
                }]
            }
        };
        this.dataLoader = this.dataLoader.bind(this)
    }

    getData = async ()=>{
        const datajson = await getDataLocalidades()
        console.log(datajson)
        let char_data = this.state.charData
        let r = Math.floor(Math.random() * 255)
        let g = Math.floor(Math.random() * 255)
        let b = Math.floor(Math.random() * 255)
        char_data.labels= datajson.localidades;
        char_data.datasets.map( dataset => {
                dataset.data = datajson.numcasosloc;
                dataset.backgroundColor = `rgba(${r},${g},${b},0.6)`
                return null
            })
        
        this.setState({charData: char_data})
        this.setState({dataIsLoaded: true})

    }

    componentDidMount(){
        this.getData()
    }

    dataLoader = () => {
        if (!this.state.dataIsLoaded) {
            return  (
            <div>
                <p>Estamos cargando los datos abiertos de Covid-19 Bogotá</p>
            </div>
            )
        }
        else return (<div></div>)
    }
    
    render(){
        let {User} = this.props;

        return(
            <div style={{height:'250px',paddingTop: '30px'}}>
                
                <this.dataLoader />
                {
                    User.Filter === 'Localidad' ? 
                    <Bar
                        data={this.state.charData}
                        width={100}
                        height={50}
                        options={{ maintainAspectRatio: false/*, legend: {display: 'hallo', position:'right'}*/ }}
                    />
                    : 'ño se'
                }
                <div className="row">
                    
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
       User: state.reducer_user
    };
};

export default connect(mapStateToProps,null)(Graphic);
