import React,{useState}  from 'react';
 
import {connect} from 'react-redux'
import {change} from '../../Redux/actions/action_user'

const Filters = (props) => {
    
    const [open,setOpen] = useState(false)
    const [label,setLabel] = useState('Filtros')
    const openMenu =()=> setOpen(!open)
    const change_label =(value)=> {
        setLabel(value)
        props.change(['Filtro',value]);
    }
    const [filter] = useState([
        {name: 'Edad'},{name: 'Sexo'},{name: 'Estado'},{name: 'Tipo de caso'},
        {name: 'Localidad'},
    ])


    

    let filters = filter.map( (e) => {
        return(
            <button 
                className="dropdown-item" 
                onClick={()=>{ change_label(e.name) }}
                type="button">{e.name}</button>
        )
    })

    return(
        
        <div className="dropdown" style={{paddingTop: '10px'}}>
            <button 
                className="btn btn-primary dropdown-toggle" 
                type="button" id="dropdownMenu2" 
                onClick={openMenu}
                data-toggle="dropdown" 
                aria-haspopup="true" 
                style={{width: '180px'}}
                aria-expanded="false">
                {label} 
            </button>
            {
                open ?
                <div className="" aria-labelledby="dropdownMenu" style={{marginLeft: '18px', width: '180px'}}>
                    {filters}
                </div>
                : null
            }
        </div>
    )
 
}

const mapDispatchToProps = (dispatch) => {
    return{
        change: (name) => {dispatch(change(name))},
        
    }
}
export default connect(null,mapDispatchToProps)(Filters);