const defaultState = {
    Localidad: 'Localidad',
    Filter: 'Localidad'
}

const userReducer = (state = defaultState, { type, payload }) => {
    switch(type){
        case 'CHANGE':
            switch(payload[0]){
                case 'Localidad':{
                    return {...state,Localidad: payload[1]}
                }
                case 'Filtro':{
                    console.log('ChangeFilter',payload[1])
                    return {...state,Filter: payload[1]}
                }
                default: return{...state,password: payload[1]}
            }
            default:
                return state
    }
}

export default userReducer;