const URL = 'https://datosabiertos.bogota.gov.co/api/3/action/datastore_search?resource_id=b64ba3c4-9e41-41b8-b3fd-2da21d627558&limit=20000'
const URL_GEO = 'https://datosabiertos.bogota.gov.co/dataset/856cb657-8ca3-4ee8-857f-37211173b1f8/resource/497b8756-0927-4aee-8da9-ca4e32ca3a8a/download/loca.geojson'

export const getDataLocalidades = async () => {
    const response = await fetch(URL)
        .then(async (response)=>{
            let data = await response.json()
            //console.log('rrr2',data.result.records)
            let dataloc = addDataBog(data.result.records)

            let casosPorGenero =   getCasosPorGenero(data.result.records);
            let casosPorEdad   =   getCasosPorEdad(data.result.records);

            //console.log('casosGenero',casosPorGenero)

            return dataloc
        }).catch(err => console.log(err))
    //console.log('fetcres',response)
    return response
}



  // casos por genero en localidades
const getCasosPorGenero =(data)=> {
    let dic_loc = {
        "Engativá": [0,0], "Usaquén" : [0,0],
        "Fontibón": [0,0], "Kennedy": [0,0],
        "Suba" : [0,0], "Chapinero": [0,0],
        "Ciudad Bolívar": [0,0], "Barrios Unidos" : [0,0],
        "La Candelaria": [0,0], "Rafael Uribe Uribe": [0,0],
        "Puente Aranda" : [0,0], "Usme" : [0,0],
        "Teusaquillo" : [0,0], "Tunjuelito" : [0,0],
        "Bosa" : [0,0], "Fuera de Bogotá" : [0,0],
        "Santa Fe" : [0,0], "Antonio Nariño" : [0,0],
        "San Cristóbal" : [0,0],"Los Mártires" : [0,0],
        "Sin Dato" : [0,0],
    }
  
    data.map( (per) => {

        var localidad = per["Localidad de residencia"];
        if(per["Sexo"] === "F") dic_loc[localidad][0]+=1
        else dic_loc[localidad][1]+=1
          
        return null
    })
   
    //console.log(dic_loc)
    return dic_loc
   
}

