// add data to bogota
const addDataBog =(data)=> {
    let NumCasos= []
    let Localidades= []
    data.map( (per) => {
        var localidad = per["Localidad de residencia"];
        if(Localidades.includes(localidad)) {
            var idx = Localidades.indexOf(localidad)
            //console.log('idx: ',idx); 
            NumCasos[idx] = NumCasos[idx] + 1
        } else {
            Localidades.push(localidad)
            NumCasos.push(1)
        }
        return null
    })
    return {
        numcasosloc: NumCasos,
        localidades: Localidades
    };;
}

// get data for polylines
export const getPositions = async () => {
    const response = await fetch(URL_GEO)
        .then(async (response)=>{
            let data = await response.json()
            //console.log('rrr2',data.result.records)
            let dataGeo = orderData(data.features)
            //console.log('numinfun',dataloc)
            return dataGeo
        }).catch(err => console.log(err))
    //console.log('fetcres',response)
    return response
}

// convert array into array of dictionaries
const convert=(data)=>{
    let array = []
    data.map((e) =>{
        let info = {}
        info['lat'] = e[1]
        info['lng'] = e[0]
        array.push(info)
        return null
    })
    return array
}


// order data for return global positions 
const orderData =(data)=>{
    let array = []
    data.map( (e) =>{
        let info = {}
        info['0']= e['properties'].LocNombre
        let c = convert(e['geometry'].coordinates['0']['0'])
        info['1']= c
        array.push(info)
        return null
    })
    return array
}

 