import {getPositions} from '../../Components/Opendata/data'; 

export const change_data = (option) =>{
    return {
        type: 'CHANGE_DATA',
        payload: option
    }
}

export const get_positions = () =>{
    console.log('get_positions redux')
    return async (dispatch) => {
        let solve = await getPositions()
        dispatch(change_data(['PolyLoc',solve]))
        /*console.log("solve from redux: ",solve)
        setTimeout(() => {
            
        }, 1000);*/
    }
}