import React,{useState}  from 'react';
import './slider.css'
 
const Slider = () => {
    
    const [value,setValue] = useState(0)
    const [month,setMonth] = useState('Marzo')
    const change =(event)=>{
        let val = event.target.value
        if (val <= 29) setMonth('Marzo')
        if (value > 29) setMonth('Abril')
        if (val > 61) setMonth('Mayo')
        if (val > 92) setMonth('Junio')
        setValue(event.target.value)
    }
    return(
        <div 
            style={
                {
                    paddingTop: '15px',paddingBottom:'15px',
                    backgroundColor: '#4888f7', color: '#ffff',
                    borderRadius: (0, 0, 20, 20)
                }
            }
            >
            <div className="row">
                <div className="col-md-10">
                    <input onChange={change} type="range" value={value} min="1" max="123" className="slider" list="tickmarks"/>
                </div>
                
                <div className="col-md-2">
                    <div style={{paddingRight:'30px', fontSize: '22px'}}>
                        { value%31 === 0 ? (value%31+1) : value%31 }
                    </div>
                    
                </div>
            </div>
            <div className="row" style={{paddingTop:'10px'}}>
                <div className="col-md-2">Marzo</div>
                <div className="col-md-3">Abril</div>
                <div className="col-md-3">Mayo</div>
                <div className="col-md-2">Junio</div>
                <div className="col-md-2" style={{paddingRight: '50px'}}>{month}</div>
            </div>
        </div>
    )
 
}
export default Slider;