const defaultState = {
    PolyLoc: []
}

const appReducer = (state = defaultState, { type, payload }) => {
    switch(type){
        case 'CHANGE_DATA':
            switch(payload[0]){
                case 'PolyLoc':{
                    console.log("POLYLOC: ",payload[1])
                    return {...state,PolyLoc: payload[1]}
                }
                default: return{...state,password: payload[1]}
            }
            default:
                return state
    }
}

export default appReducer;