import React, {useEffect, useState} from 'react'
import styles from './styles'

// Redux
import {connect} from 'react-redux'
//import {change} from '../actions/ListRol'
import {change} from '../../Redux/actions/action_user'
import {get_positions} from '../../Redux/actions/actions_app'
import store from '../../Redux/store'
import ReactDOMServer from "react-dom/server";

const googleMapScript = document.createElement('script')
let map_c;
let  polyloc = []




const GoogleMap =(props)=> {
    
    const googleMapRef = React.createRef()
    //const [polyloc,setPolyloc] = useState(null)
    const [load,setLoad] = useState(false)
    const [namel,setNamel] = useState('')
    //const {polyLoc } = useMap();
    
    const changeLoc =(name)=> {
        props.change(['Localidad',name]);
        setNamel(name)
        console.log(namel)
        //console.log('changeLoc, ',name)
    }
    

    const InfoWindowContent=(name)=>{
        return(
            <div id="content">
                <h3> {name} </h3>
                <div id="bodyContent">
                    Attribution: Uluru,
                    (last visited June 22, 2009).
                </div>
            </div>
        )
    }

    useEffect((props)=>{
        
        googleMapScript.src = `https://maps.googleapis.com/maps/api/js?key=AIzaSyAcpnxVkDPet7-hIIVQM0gBMzwfLqb2Jyk&callback=createGoogleMap`
        window.document.body.appendChild(googleMapScript)
        
        setTimeout(() => {
            //addShape()
            //props.get_positions()
            
            googleMapScript.addEventListener('load', {
                googleMap: createGoogleMap(),
                marker: createMarker({ lat: 4.638743, lng: -74.085238 }),
                //Polygon: createShape(),
                Polygon: addShape(),
                //circle: createCircle()
            })
            //polyloc = store.getState().reducer_app.PolyLoc
        }, 3000);
    },[load])
    

    const createGoogleMap = () =>{
        setLoad(true)
        //props.get_positions()
        const google = window.google;
        let Map = new google.maps.Map(googleMapRef.current, {
            zoom: 10,
            center: {
                lat: 4.60971,
                lng: -74.08175,
            },
            styles: styles,
            disableDefaultUI: true,
        });
        map_c = Map
        return Map
    }
        
    const createMarker = (data) =>{
        props.get_positions()
        const google = window.google;
        let icon = {
            url: "http://iconsetc.com/icons-watermarks/simple-purple/bfa/bfa_map-marker/bfa_map-marker_simple-purple_512x512.png", // url
            scaledSize: new google.maps.Size(50, 50),
        };
        let marker = new google.maps.Marker({
            position: data,
            //map: this.createGoogleMap(),
            map: map_c,
            icon: icon, 
        })
        return marker;
    }

    const addShape =()=>{
        setTimeout(()=>{
            polyloc = store.getState().reducer_app.PolyLoc
            var npolygon = polyloc
            console.log('addshape: polyloc ',polyloc)
            let a = []
            for( var j = 0 ; j < npolygon.length;j++){
                a.push(createShape(npolygon[j]['1'],npolygon[j]['0']))
            }
            return a
        }, 4000)
    }

     
    const createShape =(path,name) =>{
        const google = window.google;
        var neighbor = new google.maps.Polygon({
            paths: path,
            strokeColor: '#147ef7',
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: '#147ef7',
            fillOpacity: 0.5
        });
        

        neighbor.setMap(map_c);
        neighbor.addListener('click', showData);
        
        //let infoWindow = new google.maps.InfoWindow();
        const infowindow = new window.google.maps.InfoWindow({
            content: ""
        });

        function showData(event) {
            changeLoc(name)

            const content = ReactDOMServer.renderToString(InfoWindowContent(name));
            infowindow.setContent(content);
            infowindow.setPosition(event.latLng);
            infowindow.open(map_c)
            /*infoWindow.setContent(dataNeigh);
            infoWindow.setPosition(event.latLng);
            infoWindow.open(map_c);*/
        }
        return neighbor.setMap(map_c)
    }
    
    const createCircle =()=>{
        const google = window.google;
        var circle = new google.maps.Circle({
            strokeColor: "#FF0000",
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: "#FF0000",
            fillOpacity: 0.35,
            map: map_c,
            center: {lat: 4.638743, lng: -74.085238},
            radius: 10000
        });
        return circle
    }

    return (
        <div
            id="google-map"
            ref={googleMapRef}
            style={{ width: '810px', height: '660px', borderRadius: '20px'}}
        />
            
        
    )
}

const mapStateToProps = (state) => {
    return {
       App: state.reducer_app
    };
};

const mapDispatchToProps = (dispatch) => {
    return{
        get_positions: () => {dispatch(get_positions())},
        change: (name) => {dispatch(change(name))},
        
    }
}


export default (connect(mapStateToProps, mapDispatchToProps)(GoogleMap));

